.button {
  font-size: 1.8rem;
  font-weight: bold;
  border: none;
  border-radius: 24px;
  padding: 1.2rem 5rem;
  cursor: pointer; }

.button_green {
  background-color: $green;
  color: $white;
  &:hover {
    background-color: $greenHover; } }


.button_gray {
  background-color: $gray;
  color: $violet;
  &:hover {
    background-color: $grayHover; } }

.button_light {
  width: 4.8rem;
  height: 4.8rem;
  padding: 0;
  background-color: $gray3;
  color: $white; }

.button_white {
  background-color: $white;
  color: $red; }

.button_circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4.8rem;
  height: 4.8rem;
  border-radius: 50%;
  padding: 0; }

.button_play {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 11.2rem;
  height: 11.2rem;
  border-radius: 50%;
  background-color: $white;
  color: $violet;
  &:hover {
    background-color: $gray; } }
