.subject {
  display: flex;
  flex-direction: column;
  max-width: 370px;
  background-color: $gray2;
  border-radius: 2rem;
  overflow: hidden;
  position: relative;
  .alert {
    position: absolute;
    top: 3.6rem;
    left: 3.2rem; }
  &__image {
    width: 100%;
    height: 304px;
    border-radius: 2rem;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__content {
    padding: 2.3rem 3.2rem 3.2rem; }
  &__author {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $black;
    padding: 0.3rem 0 0.6rem; }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &__lessons {
    @extend %textСomplementary; } }

.expert {
  max-width: 270px;
  background-color: $gray2;
  border-radius: 2rem;
  margin: 112px 0 0 52px;
  &__photo {
    width: 27rem;
    height: 27rem;
    position: relative;
    top: -112px;
    left: -52px;
    margin-bottom: -112px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__about {
    padding: 2.5rem 3.2rem 4.8rem; }
  &__prof {
    @extend %textСomplementary; }
  &__social {
    display: flex;
    .button {
      margin-right: 0.8rem;
      color: $violet; } } }
